.winnerBracket {
  box-sizing: border-box;
  width: 4000px !important;
}

.winnerBracket .bracket {
  background: #00061b;
  padding: 10px 20px;
  width: 300px;
  box-sizing: border-box;
  position: relative;
}

.winnerBracket .round-1 {
  margin-top: 50px;
}

.winnerBracket .round-2 {
  margin-top: 108px;
}

.winnerBracket .round-2 .bracket-box:before {
  position: absolute;
  content: "";
  width: 45px;
  height: 2px;
  left: 0;
  top: 50%;
  background-color: #fff;
  transform: translateX(-100%);
}

.winnerBracket .round-2 ~ .round-2 {
  margin-top: 168px;
}

.winnerBracket .round-empty {
  position: relative;
  margin-top: 331px;
  height: 2px;
  background: #FFF;
}

.winnerBracket .round-empty:before {
  position: absolute;
  content: "";
  width: 45px;
  height: 2px;
  left: 0;
  background-color: #fff;
  transform: translateX(-100%);
}

.winnerBracket .round-4 {
  margin-top: 225px;
}

.winnerBracket .round-4 .bracket-box:before {
  position: absolute;
  content: "";
  width: 59px;
  height: 2px;
  left: 0;
  top: 50%;
  background-color: #fff;
  transform: translateX(-100%);
}

.winnerBracket .round-5 {
  margin-top: 225px;
}

.winnerBracket .round-Finale {
  margin-top: 225px;
}

.winnerBracket .bracket:after, .winnerBracket .bracket:before {
  content: "";
  position: absolute;
  background-color: #fff;
  top: 50%;
}

.winnerBracket .bracket:before {
  right: 0;
  transform: translateX(100%);
  width: 25px;
  height: 2px;
}

.winnerBracket .bracket:after {
  right: -25px;
  height: calc(50% + 25px);
  width: 2px;
}

.winnerBracket .bracket:nth-child(odd):after {
  transform: translateY(-100%);
}

.winnerBracket .round-2:after {
  position: absolute;
  content: "";
  width: 2px;
  height: calc(50% + 85px);
  right: -27px;
  top: 50%;
  background-color: #fff;
  transform: translateX(-100%);
}

.winnerBracket .round-2:nth-child(odd):after {
  transform: translateY(-100%);
  right: -25px !important;
}

.winnerBracket .round-4:after, .winnerBracket .round-Finale:after {
  width: 0px;
}

